import React from 'react';
import {
    Card,
    CardHeader,
    CardBody,
    Table, Button,
} from 'reactstrap';
import { getReportOutputs } from './api';
import moment from 'moment';
import { ad2bs } from 'ad-bs-converter';

export default class ReportOutputs extends React.Component {

    state = {
        outputs: []
    }

    componentDidMount() {
        getReportOutputs({
            type: 'branch-summary'
        }).then(outputs => {
            this.setState({
                outputs: outputs
            });
        }).catch(() => { });
    }

    downloadFile = path => {
        window.open(process.env.REACT_APP_SERVER_URL + `/${path}`);
    }

    render() {
        return (
            <div className="animated fadeIn">
                <Card>
                    <CardHeader>
                        Branch Summary Report Download Log
                    </CardHeader>
                    <CardBody>
                        <Table>
                            <thead>
                                <tr>
                                    <th>S.No.</th>
                                    <th>Date Created (AD)</th>
                                    <th>Date Created (BS)</th>
                                    <th>Type</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.outputs.map((o, i) => {
                                    const adDate = moment(o.createdAt).format("YYYY/MM/DD")
                                    const bs = ad2bs(adDate).en;
                                    const bsDate = bs.year + "-" + bs.month + "-" + bs.day;
                                    return (
                                        <tr key={i}>
                                            <td>{i + 1}</td>
                                            <td>{adDate}</td>
                                            <td>{bsDate}</td>
                                            <td>{o.type}</td>
                                            <th>
                                                <Button color="info text-white" size="sm" onClick={() => this.downloadFile(o.path)}>
                                                    <i className="fa fa-download" />
                                                </Button>
                                            </th>
                                        </tr>
                                    );
                                })}
                            </tbody>
                        </Table>
                    </CardBody>
                </Card>
            </div>
        );
    }

}